.main-nav {
  z-index: 3;
  min-height: 0;
  font-weight: 500;
  line-height: 16px;
}
@media (min-width: 768px) {
  .main-nav {
    margin-right: 19px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    margin-right: 20px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  display: block;
  width: 60px;
  height: 60px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 5px 0 0 #333333, 0 10px 0 0 #333333;
}
.main-nav--closed .main-nav__toggle:active {
  background-color: #f0ebff;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    box-shadow: 0 -1px 0 #f0ebff;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .user-list {
    top: 0;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  right: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__wrapper::before {
  content: "";
  position: absolute;
  top: -60px;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 60px;
  background: #ffffff;
  box-shadow: 0 1px 0 #f0ebff;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__wrapper::before {
    content: none;
  }
}
.main-nav__wrapper {
  position: absolute;
  top: 60px;
